@mixin overlay($pos : absolute)
  position: $pos
  left: 0
  top: 0
  width: 100%
  height: 100%
  background-color: rgba(255,255,255,.1)
@mixin color-scheme-generator($clr)
  --ring-clr: #{$clr}
  --ring-500: #{$clr}#{33}
  --ring-400: #{$clr}#{22}
  --ring-300: #{$clr}#{2d}
  --ring-100: #{$clr}#{11}
