.intro-container
  display: flex
  justify-content: center
  align-items: center
  flex-direction: column
  width: 100%
  height: 100%
  position: absolute
  left: 0
  top: 0
  bottom: 0
  right: 0
  color: #009688
  text-align: center
  overflow: hidden
  &::before,
  &::after
    content: ""
    width: 200px
    height: 200px
    position: absolute
  &::before
    background-color: #2343
    border-radius: 100%
    right: 0
    top: 0
    transform: translate(40%,-40%)
  &::after
    background-color: #4545
    left: 0
    bottom: 0
    transform: translate(-40%,40%)
    border-radius: 30% 70% 70% 30% / 30% 30% 70% 70%
  h1
    font-size: 2.5rem
    margin-bottom: 15px
  .disc
    font-size: 1.3em
    margin-bottom: 30px
  .new-quiz
    font-size: 1.2rem
