@import '../helpers/mixins'
@import '../helpers/animations'
.loading-container
  position: fixed
  left: 0%
  top: 0%
  display: flex
  justify-content: center
  align-items: center
  width: 100%
  height: 100%
  background-color: rgba(0, 0, 0, .5)
  .ring
    width: 80px
    height: 80px
    border-radius: 50%
    border: 3px solid #0000
    margin: -10px
    position: relative
    border-top: 4px solid var(--ring-clr)
    animation: spining 3s infinite linear
    &::before
      content: ''
      position: absolute
      left: 5px
      top: 5px
      border-radius: 50%
      width: 10px
      height: 10px
      background-color: var(--ring-clr)
      box-shadow: 0 0 0 2px var(--ring-500),0 0 0 4px var(--ring-400),0 0 0 7px var(--ring-300),0 0 0 7px var(--ring-100),0 0 0 10px var(--ring-300)
    &:first-child
      @include color-scheme-generator(#34ace0)
      &::before
        left: unset
        right: 5px
    &:nth-child(2)
      @include color-scheme-generator(#f6b93b)
      top: -40px
      animation-direction: reverse
      animation-delay: -1s
    &:nth-child(3)
      @include color-scheme-generator(#33d9b2)
      animation-direction: reverse
      animation-delay: -2s
