@import '../helpers/mixins'
.popup
  @include overlay($pos: fixed)
  display: flex
    z-index: 100
  justify-content: center
  align-items: center
  .card
    text-align: center
    font-size: 1.2em
    max-width: 365px
    width: calc(100% - 30px)
    border: 1px solid #aaa
    background-image: linear-gradient(125deg ,#010332, #100328,#29011C, #43000D,#530005)
    color: #fff
    padding: .7em
    padding-bottom: .4em
    border-radius: .4em
  .popup-message
    font-size: 1.2em
    &::first-letter
      font-size: 1.01em
      text-transform: upperCase
  .close-popup
    font-size: 1.15em
    display: block
    background-color: #29011C
    cursor: pointer
    opacity: .8
    border: none
    color: #fff
    border: 1px solid #fff
    border-radius: .2em
    transition-duration: 250ms
    position: relative
    margin:
      left: auto
      top: 1.4em
    padding:
      block: .2em
      inline: .7em
    @media (hover: hover)
      &:hover
        color: hsl(0, 60%, 40%)
        background-color: #D7CEAC
