.quiz-container
  display: flex
  flex-direction: column
  align-items: center
  text-align: center
  padding: 30px
  line-height: 1.2
  gap: 15px
  min-height: 90vh
  .quiz
    padding-bottom: 15px
    width: 100%
    user-select: none
    &:not(:last-of-type)
      border-bottom: 1px solid #454545
    h4
      font-size: 1.3rem
      color: #{'hsl(205deg 90% 90%)'}
      &::first-letter
        text-transform: capitalize
    .options
      display: flex
      flex-wrap: wrap
      justify-content: center
      gap: 10px
      margin-top: 15px
      .option
        outline: 1px solid #{'hsl(205deg 90% 90%)'}
        border-radius: 5px
        padding: .4em .7em
        cursor: pointer
        transition-duration: 400ms
        &:hover
          background-color: #fff
          color: #000
        &.active
          background-color: #296ca3
          color: #fff
          outline: none
        &.correct
          color: #222
          background-color: #94D7A2
          outline: none
        &.wrong
          background-color: #F8BCBC
          color: #662121
          outline: none
          opacity: .5