*
  margin: 0
  box-sizing: border-box
  padding: 0
body
  background-color: #1c1c1c
  font-family: 'Karla', sans-serif
  color: #fefefe
  margin: 0
.btn
  background-color: #4D5B9E
  border: none
  color: white
  border-radius: .5em
  font-size: 1rem
  padding: .8em 2em
  cursor: pointer
  transition: 400ms
  &:hover
    opacity: .8
    background-color: #475285
.worning
  color: #eab543
  font-size: 0.8rem
  font-family: monospace
  background: #222
  padding: .3em
  text-transform: capitalize
  transform-origin: left
  transition: 400ms
.quiz-end .score
  font-size: 1.18em
  font-weight: 500
  text-transform: capitalize
